<template>
    <div>
        <mu-circular-progress :size="40" v-if="loadingArticle"/>
        <div class="article">
            <h1>{{article.title}}</h1>
            <div class="article-info">
                <span class="info">{{article.source}}</span>
                <span class="info">{{article.time}}</span>
            </div>
            <div class="summary" v-show="article.summary">{{article.summary}}</div>
            <div id="art-content">
                <div v-for="(p, i) in article.content" :key="i">
                    <div class="txt block" v-if="p.type == 'txt'" v-html="p.value"></div>
                    <div class="block" v-if="p.type == 'img'">
                        <img :src="p.value">
                    </div>
                </div>
            </div>
    </div>

        <!--<div class="article-footer">
            <div class="read">
                <span>阅读：{{article.watches}}</span>
            </div>
            <div class="raw">
                <span>阅读原文</span>
            </div>
        </div>
        <div class="article-statement">
            <span class="verline"></span>
            <span class="statement">本观点不代表北京时间立场</span>
            <span class="verline"></span>
        </div>
        <div class="comment">
            <h2 class="h2-title">
                热门评论
                <i>/</i>
                <span class="verline"></span>
            </h2>
            <ul class="comment-list empty-comment"></ul>
        </div>-->
    </div>
</template>

<script>
    import axios from "axios"
    import jsonp from 'jsonp'

    import {mapActions, mapGetters} from 'vuex'

    export default {
        data() {
            return {

            }
        },
        computed: {
            ...mapGetters(["article", "loadingArticle"])
        },
        methods: {
            ...mapActions(["getArticle"])
        },
        created() {
            console.log("新闻列表传来的参数：", this.$route.params.id);
        },
        beforeRouteEnter(to, from, next) {
            next(function (vm) {
                console.log(456)
                vm.getArticle(vm.$route.params.id);
            })
        },
        beforeRouteLeave (to, from, next) {
            console.log("离开了")
            this.$store.commit("setArticle");
            this.$store.commit("setLoadingArticle", true);
            next();
        }
    }
</script>

<style>
    .app-view .mu-circular-progress {
        position: absolute;
        left: 45%;
        top: 35%;
        z-index: 200;
    }

    .article {
        position: relative;
        padding: 0 10px;
        width: 100%;
        box-sizing: border-box;
        /*height:1318px;*/
    }

    .article h1 {
        padding: 10px 0;
        font-weight: 700;
        color: #333;
        line-height: 1.5;
        font-size: 24px;
        text-align: left;
    }

    .article-info {
        color: #999;
        margin-bottom: 12px;
    }

    .article-info .info {
        margin-right: 10px;
    }

    .article .summary {
        line-height: 1.7;
        width: 100%;
        border: 1px solid #eee;
        margin: 0 0 15px;
        text-align: left;
        padding: 15px;
        box-sizing: border-box;
        color: #999;
    }

    .article .block {
        margin: 0 0 15px;
        cursor: pointer;
    }

    .article p, .article #art-content div.txt {
        font-size: 18px;
        line-height: 1.6em;
        text-align: justify;
        color: rgba(0,0,0,.87);
        word-wrap: break-word;
        text-indent: 2em;
    }

    .block.img {
        background-color: #eee!important;
    }

    .article img {
        max-width: 100%;
        cursor: pointer;
    }

    .article .article-footer {
        width: 100%;
        overflow: hidden;
        color: #999;
    }

    .article .article-footer .read {
        float: left;
    }

    .article .article-footer .raw {
        float: right;
    }

    .article .article-statement {
        display: flex;
        width: 100%;
        margin: 20px 0;
        text-align: center;
    }

    .article .article-statement span {
        -webkit-box-flex: 1;
        -webkit-flex: 1;
        flex: 1;
        display: block;
    }

    .article .article-statement .statement {
        color: #bbb;
        font-size: 11px;
        white-space: nowrap;
        padding: 0 10px;
    }

    .verline {
        position: relative;
    }

    .verline::before {
        height: 1px;
        width: 100%;
        position: absolute;
        background-color: #eee;
        content: '';
        left: 0;
        top: 45%;
    }

    .article .comment {
        width: 100%;
        position: relative;
        font-size: 16px;
    }

    .article .comment .h2-title {
        display: flex;
        margin: 15px 0;
    }

    .article .comment .h2-title i {
        padding-left: 5px;
        color: #ff3644
    }

    .article .h2-title span {
        margin-left: 5px;
        -webkit-box-flex: 1;
        -webkit-flex: 1;
        flex: 1;
        display: block;
    }

    .empty-comment {
        background: url(http://p9.qhimg.com/t010e7c0748ccee9b75.png) center center no-repeat;
        background-size: 50% auto;
        min-height: 160px;
        width: 100%;
        box-sizing: border-box;
        position: relative;
        padding-bottom: 15px;
    }

    .empty-comment::before {
        content: '还没有相关评论呢，快来评论吧';
        text-align: center;
        position: absolute;
        width: 100%;
        bottom: 0;
        left: 0;
        color: #bbb;
        font-size: 14px;
    }
</style>



