export default {
	props: {
		scroller: {
			type: [ HTMLDocument, Element, Window ],
			default () {
				return window
			}
		}
	},
	mounted() {
        console.log("1.触发了mounted...")
		this.$bindScroll()
	},
	methods: {
		$bindScroll() {
            console.log("2.触发了$bindScroll...")
			if ( !this.scroller ) return
            console.log("3.触发了$bindScroll...")
			this._handleScroll = ( e ) => {
				if ( this.onScroll ) this.onScroll() //滑动屏幕回调函数
			}
			this.scroller.addEventListener( 'scroll', this._handleScroll )
            console.log("window:", this.scroller)
		},
		$unbindScroll( scroller ) {
            console.log("4.触发了$unbindScroll...")
			scroller = scroller || this.scroller
			if ( this._handleScroll ) scroller.removeEventListener( 'scroll', this._handleScroll )
		}
	},
	beforeDestroy() {
		this.$unbindScroll()
	},
	watch: {
		scroller( scroller, oldScroller ) {
            console.log("5.触发了watch1...", scroller)
            console.log("5.触发了watch1...", oldScroller)
			if ( scroller === oldScroller ) return
            console.log("6.触发了watch2...")
			this.$unbindScroll( oldScroller )
			this.$bindScroll( scroller )
		}
	}
}



