<template>
    <div>
        <div id="header">
            <div class="header-wrapper">
                <mu-appbar title="焕铎新闻">
                    <mu-icon-button icon="menu" slot="left"/>
                    <mu-icon-button icon="expand_more" slot="right"/>
                </mu-appbar>
                <div id="navbar">
                    <div class="navbar-wrapper">
                        <ul>
                            <li v-for="(channel, index) in channels" :key="index">
                                <router-link :to="{path: channel.cid}">
                                    {{channel.cname}}
                                </router-link>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>

        <mu-circular-progress :size="40" v-if="!showList || loadingList"/>


        <div id="list" v-show="showList && !loadingList" style="position: relative;">

            <mu-refresh-control :refreshing="refreshing" :trigger="trigger" @refresh="refresh"/>


            <div v-for="(news, index) in newsListData" :key="index" style="margin-top: 5px;margin-bottom: 5px;">
                <div v-if="news.module == 7"
                     class="content_item content-card" style="margin-top: 5px;margin-bottom: 5px;">
                    <div class="card-title">{{news.data.title}}</div>
                    <div class="card-list">
                        <div v-for="(card_news, key) in news.data.news" :key="key" class="content_item common">
                            <router-link :to="{path: 'article/' + card_news.gid}">
                                <div class="content_main">
                                    <div class="info">
                                        <div class="title">{{card_news.data.title}}</div>
                                        <div class="tips">
                                            <div class="tip">{{card_news.data.source}}</div>
                                            <div class="tip">{{card_news.data.pdate}}</div>
                                        </div>
                                    </div>
                                    <div class="img"
                                         v-bind:style="{backgroundImage:'url(' + card_news.data.covers[0] + ')'}"></div>
                                </div>
                            </router-link>
                        </div>
                    </div>
                </div>

                <div v-if="news.module != 7"
                     class="content_item"
                     style="margin-top: 5px;margin-bottom: 5px;">
                    <router-link :to="{path: 'article/' + news.gid, params: {gid: news.gid}}">
                        <div class="content_main">
                            <div class="info">
                                <div class="title">{{news.data.title}}</div>
                                <div class="tips">
                                    <div class="tip">{{news.data.source}}</div>
                                    <div class="tip">{{news.data.pdate}}</div>
                                </div>
                            </div>
                            <div class="img" v-show="news.module != 2"
                                 v-bind:style="{backgroundImage:'url(' + news.data.covers[0] + ')'}">
                                <div v-if="news.type == 3 || news.type == 6" class="play-icon"></div>
                                <div v-if="news.type == 3 || news.type == 6" class="tip">{{news.data.duration}}</div>
                            </div>
                        </div>
                    </router-link>
                </div>
            </div>

            <mu-infinite-scroll :scroller="scroller" :loading="isLoadMore" @load="load" loadingText="你好啊..."/>

        </div>

        <!--<infinite-scroll  :scroller="scroller" :loading="isLoadMore" @load="load" />-->

    </div>
</template>

<script>
    import axios from 'axios'

    import {mapGetters, mapActions} from 'vuex'

    export default {
        data () {
            return {
                scroller: null,
                trigger: null,
                isRefreshing: false,
                path: this.$route.params.id
            }
        },
        mounted() {
            this.scroller = this.$el//document.getElementById("list2");
            this.trigger = document.getElementById("list");
            this.getChannels();
            this.getNewsList({cid: this.$route.params.id});
            console.log("$route：", this.$route);
            console.log("this.$el：", this.$el);
        },
        computed: {
            ...mapGetters([
                 "channels",
                 "newslist",
                 "showList",
                 "loadingList",
                 "isLoadMore",
                 "refreshing"
            ]),
            newsListData: function() {
                return this.newslist[this.$route.params.id];
            },
        },
        methods: {
            ...mapActions([
                "getChannels",
                "getNewsList",
                "loadMore",
                "refreshMore"
            ]),
            load() {
                console.log("加载更多触发了...");
                console.log("scrollHeight:", this.$el.scrollHeight);
                if (this.$el.scrollHeight != 0) {
                    if (this.newslist[this.$route.params.id].length>0) {
                        this.loadMore(this.$route.params.id);
                    }
                }

                /*this.$store.commit("setIsLoadMore", true);
                console.log("到底了");*/
            },
            refresh() {
                console.log("触发了...");
                console.log(document.body.scrollTop);
                /*console.log("正在刷新...");
                this.isRefreshing = true;
                setTimeout(() => {
                    console.log("刷新完成...");
                    this.isRefreshing = false;
                }, 2000)*/
                if (document.body.scrollTop == 0) {
                    this.refreshMore(this.$route.params.id);
                }

            }
        },
        watch: {
            '$route': function() {
                if (/^\/\w+$/.test(this.$route.path)) {
                    if (!(this.newslist[this.$route.params.id].length>0)) {
                        this.getNewsList({cid: this.$route.path.slice(1)});
                        //this.$store.commit("setShowList", false);
                        this.$store.commit("setLoadingList", true);
                    }
                }
                console.log("$route路由变化了：", this.$route.path);
            }
        }
    }
</script>

<style>
    .mdui-card {
        position: relative;
        box-sizing: border-box;
        overflow: hidden;
        color: #000;
        background-color: #fff;
        border-radius: 2px;
        box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .2), 0 2px 2px 0 rgba(0, 0, 0, .14), 0 1px 5px 0 rgba(0, 0, 0, .12);
    }

    #header {
        position: relative;
        height: 96px;
        z-index: 100;
    }

    #header .header-wrapper {
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        z-index: 100;
    }

    #navbar {
        width: 100%;
        height: 40px;
        line-height: 40px;
        background-color: #7e57c2;
        white-space: nowrap;
    }

    #navbar .navbar-wrapper {
        height: 40px;
        overflow: hidden;
    }

    #navbar ul {
        overflow-x: scroll;
    }

    #navbar li {
        display: inline-block;
    }

    #navbar li a {
        display: block;
        padding: 0 15px;
        color: #EEEEEE;
    }

    #navbar li .router-link-active {
        font-size: 110%;
        font-weight: bold;
        color: #fff;
    }
    /*-------------------------*/

    .mu-circular-progress {
        position: absolute;
        left: 45%;
        top: 35%;
        z-index: 200;
    }
    /*------------------------*/

    .content-card > .card-title {
        padding: 0.53333rem;
        border-bottom-width: 1px;
        border-bottom-style: solid;
        display: -webkit-box;
        -webkit-box-align: center;
        font-size: 16px;
        background-color: #ffffff;
        border-color: #eeeeee;
        color: #333333;
    }

    .content-card > .card-title:before {
        content: ' ';
        width: 3px;
        height: 14px;
        margin-right: 5px;
        background-color: red;
        display: block;
        border-radius: 5px;
    }

    /*-----------------type=1-----------------*/
    .mu-list {
        padding: 0;
    }
    .mu-item {
        padding: 0;
    }

    .content_item {
        padding: 0 10px;
        box-sizing: border-box;
        position: relative;
        background-color: #ffffff;
    }

    .content_item .content_main {
        display: -webkit-box;
        -webkit-box-align: start;
    }

    .content_item .content_main {
        border-bottom-style: solid;
        border-bottom-width: 1px;
        padding: 15px 0;
        position: relative;
        border-color: #eeeeee;
    }

    .content_item .content_main .info {
        -webkit-box-flex: 1;
        position: relative;
        overflow: hidden;
        height: 70px;
    }

    .content_item .content_main .title {
        color: #333333;
        font-size: 18px;
        line-height: 1.2;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-line-clamp: 2;
        -moz-line-clamp: 2;
        -webkit-box-orient: vertical;
        -moz-box-orient: vertical;
        display: -webkit-box;
        word-break: break-all;
    }

    .content_item .content_main .tips {
        margin-top: 10px;
        display: -webkit-box;
        position: absolute;
        bottom: 0;
        left: 0;
        font-size: 12px;
        color: #999999;
    }

    .content_item .content_main .tips .tip {
        margin-right: 5px;
    }

    .content_item .content_main .img {
        width: 150px;
        height: 70px;
        background-size: cover;
        background-position: center center;
        background-color: #eee;
        margin-left: 10px;
        position: relative;
    }

    /*------------------type3-module1-------------------------*/
    .content_item .content_main .img .play-icon {
        width: 40px;
        height: 40px;
        background-size: 100% 100%;
        background-position: center center;
        background-image: url("/static/images/play.png");
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -20px;
        margin-left: -20px;
    }

    .content_item .content_main .img > .tip {
        padding: 2px 5px 2px;
        background-color: rgba(0, 0, 0, 0.7);
        border-radius: 5px;
        position: absolute;
        right: 5px;
        bottom: 5px;
        font-size: 10px;
        color: #ffffff;
    }



</style>



