<template>
    <div id="app">
        <keep-alive>
            <router-view class="app-view"></router-view>
        </keep-alive>
    </div>
</template>

<script>
    export default {
        name: 'app',
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        }
    }
</script>

<style>
    .app-view {
        height: 100vh;
        overflow: auto;
        -webkit-overflow-scrolling: touch;
    }
</style>



