export default {
    
    getChannels(state, payload) {
        //console.log("来自于mutations的打印", payload)
        //state.channels = payload;
        for (let i=0; i<payload.length; i++) {
            state.channels.push(payload[i]);
        }
        //state.showList = true;
    },
    
    getNewsList(state, payload) {
        //console.log("mutations-getNewsList：", payload.data);
        //console.log("mutations-getNewsList：", payload.cid);
        /*if (!state.newslist[payload.cid]) {
            state.newslist[payload.cid] = new Array();
        }*/
        console.log("来自于mutations的打印", payload.data)
        for (let i=0; i<payload.data.length; i++) {
            state.newslist[payload.cid].push(payload.data[i]);
            console.log(`type:${payload.data[i].type}`);
        }
        //state.loadingList = false;
    },
    
    refreshMore(state, payload) {
        for (let i=0; i<payload.data.length; i++) {
            state.newslist[payload.cid].unshift(payload.data[i]);
        }
    },

    getArticle(state, payload) {
        state.article = payload;
    },
    
    setArticle(state) {
        state.article = {};
    },
    
    setShowList(state, payload) {
        state.showList = payload;
    },

    setLoadingList(state, payload) {
        state.loadingList = payload;
    },

    setLoadingArticle(state, payload) {
        state.loadingArticle = payload;
    },
    
    setIsLoadMore(state, payload) {
        state.isLoadMore = payload;
    },
    
    setRefreshing(state, payload) {
        state.refreshing = payload;
    }
    
    
    
}


