import Vue from 'vue'
import App from './App.vue'

import store from './store/index'

import router from "./router/router.js"

import infinite from './component/InfiniteScroll/infiniteScroll.vue'
Vue.component( infinite.name, infinite );

//import ElementUI from 'element-ui'
//import 'element-ui/lib/theme-default/index.css'

//import iView from 'iview';
//import 'iview/dist/styles/iview.css';

import MuseUI from 'muse-ui'
import 'muse-ui/dist/muse-ui.css'

//Vue.use(ElementUI)
//Vue.use(iView);
Vue.use(MuseUI)

new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App)
});

let indexScrollTop = 0;
let dom = document.querySelector( '.app-view' );
router.beforeEach( ( to, from, next ) => {
    console.log("to.path", to.path)
    if ( !(/^\/((?:[^\/]+?))(?:\/(?=$))?$/i.test(to.path))) {
        console.log("1.这里被执行了...", from.matched[0].regex)
        dom = document.querySelector( '.app-view' );
        indexScrollTop = dom.scrollTop;
        console.log("indexScrollTop:", indexScrollTop)
    } else {
        console.log("2.这里被执行了...")

    }
    next()
} )
router.afterEach( ( to, from, next ) => {
    if ( !(/^\/((?:[^\/]+?))(?:\/(?=$))?$/i.test(to.path)) ) {
        dom.scrollTop = 0;
        console.log("1.aftereach，这里被执行了...")
    } else {
        Vue.nextTick( () => {
            dom.scrollTop = indexScrollTop;
        } );
        console.log("2.aftereach，这里被执行了...")
    }
} );



