import Vue from "vue"
import VueRouter from "vue-router"

import Article from "../views/Article.vue"
import Test from "../views/Test.vue"
import T from "../views/t.vue"

Vue.use(VueRouter)

let routes = [
    {
        path: "/:id",
        component: Test
    },
    {
        path: "/article/:id",
        component: Article
        /*{
            template: "<div>我是文章详细内容的路由</div>"
        }*/
    },
    {
        path: "/",
        redirect: "/2da8f707e9d514c562162540d2b0ad57"
    },
]

const router = new VueRouter({
    routes
});

export default router;


