import axios from 'axios'
import jsonp from 'jsonp'

export default {

    getChannels({commit, state}) {
        axios.get("https://api.app.btime.com/channel/getchannel")
            .then(function (res) {
                let data = res["data"]["data"]["channel"][0]["list"];
                commit("getChannels", data);
                commit("setShowList", true);
            })
    },

    getNewsList({commit}, payload) {
        /*axios.get("http://h5.btime.com/index/list?cid=" + payload.cid + "&offset=0&refresh_type=1&refresh=1&count=12")
            .then(function (res) {
                commit("getNewsList", {
                    data: res.data.data,
                    cid: payload.cid
                })
            })*/
        //jsonp("http://h5.btime.com/index/list?cid=" + payload.cid + "&offset=0&refresh_type=1&refresh=1&count=12",
        jsonp(`https://h5.btime.com/index/list?cid=${payload.cid}&offset=0&refresh_type=1&refresh=1&count=12`,
                function (err, res) {
                    console.log("err", err)
                    commit("getNewsList", {
                        data: res.data,
                        cid: payload.cid
                    })
                    commit("setLoadingList", false);
                })
    },

    getArticle({commit}, payload) {
        jsonp(`https://api.btime.com/trans?gid=${payload}&token=1ffeeeb68d0daf22c3af3a957dba5b7c&sign=c14b395`,
                function (err, res) {
                    //commit("setIsLoadMore", false);
                    console.log("请求文章是触发的err", err);
                    commit("getArticle", res.data);
                    commit("setLoadingArticle", false);
                    console.log("请求到的文章：", res.data);
                })
    },

    loadMore({commit}, payload) {
        commit("setIsLoadMore", true);
        jsonp(`https://h5.btime.com/index/list?cid=${payload}&offset=0&refresh_type=1&refresh=1&count=3`,
            function (err, res) {
                console.log("loadMore执行了..", err)
                commit("getNewsList", {
                    data: res.data,
                    cid: payload
                });
                commit("setIsLoadMore", false);
                if (err) {
                    console.log("出错了...")
                }
            })
    },

    refreshMore({commit}, payload) {
        commit("setRefreshing", true);
        jsonp(`https://h5.btime.com/index/list?cid=${payload}&offset=0&refresh_type=1&refresh=1&count=12`,
            function (err, res) {
                commit("refreshMore", {
                    data: res.data,
                    cid: payload
                });
                commit("setRefreshing", false);
            })
    }

}



